'use strict';

import { Component } from 'react';
import { ReportFilters } from '@woocommerce/components';

export default class DashboardFilters extends Component {
	render() {
		const {
			path,
			query,
			filters,
			onDateSelect,
			onFilterSelect,
		} = this.props;

		return (
			<ReportFilters
				query={ query }
				path={ path }
				filters={ filters }
				onDateSelect={ onDateSelect }
				onFilterSelect={ onFilterSelect }
				advancedFilters={ {} }
				report="affiliates"
			/>
		);
	}
}
