'use strict';

/* global yith_wcaf */

import { PANEL_URL } from '../config';

const TABS = {
	dashboard: [],
	affiliates: [ 'affiliates-list', 'affiliates-rates', 'affiliates-clicks' ],
	commissions: [ 'commissions-list', 'commissions-payments' ],
	settings: [
		'settings-general',
		'settings-affiliates-registration',
		'settings-affiliates-dashboard',
		'settings-commissions-payments',
	],
};

export const getPanelUrl = ( tab, subtab, args ) => {
	const url = new URL( PANEL_URL );

	if ( tab && Object.keys( TABS ).includes( tab ) ) {
		url.searchParams.set( 'tab', tab );

		if ( subtab && TABS[ tab ].incudes( subtab ) ) {
			url.searchParams.set( 'subtab', subtab );
		}
	}

	args = args || {};

	for ( const key in args ) {
		url.searchParams.set( key, args[ key ] );
	}

	return url.href;
};
