import { Component } from 'react';
import { Fragment } from '@wordpress/element';
import { Chart } from '@woocommerce/components';
import { _x } from '@wordpress/i18n';
import {
	getAllowedIntervalsForQuery,
	getIntervalForQuery,
	getCurrentDates,
	getChartTypeForQuery,
	getDateFormatsForInterval,
	getPreviousDate,
} from '@woocommerce/date';
import { formatDate, formatTimeDiff } from '../lib/dates';
import { formatCurrency, formatPercent } from '../lib/numbers';

export default class DashboardChart extends Component {
	getChartData() {
		const mode = this.getChartMode();

		return 'item-comparison' === mode
			? this.getItemChartData()
			: this.getTimeChartData();
	}

	getTimeChartData() {
		const { query, stats, selectedChart, isRequesting } = this.props;

		if ( isRequesting ) {
			return [];
		}

		const { primary, secondary } = getCurrentDates( query ),
			intervals = stats?.primary?.data?.intervals;

		if ( ! intervals ) {
			return [];
		}

		return intervals.map( function ( interval, index ) {
			const secondaryData = stats?.secondary?.data,
				secondaryInterval = secondaryData?.intervals?.[ index ];

			let secondaryDate;

			if ( secondaryInterval ) {
				secondaryDate = secondaryInterval.date_start;
			} else {
				secondaryDate = getPreviousDate(
					interval.date_start,
					primary.after,
					secondary.after,
					query.compare,
					getIntervalForQuery( query )
				).format( 'YYYY-MM-DD HH:mm:ss' );
			}

			return {
				date: formatDate( 'Y-m-d\\TH:i:s', interval.date_start ),
				primary: {
					label: `${ primary.label } (${ primary.range })`,
					labelDate: interval.date_start,
					value: interval.subtotals[ selectedChart.key ] || 0,
				},
				secondary: {
					label: `${ secondary.label } (${ secondary.range })`,
					labelDate: secondaryDate,
					value:
						secondaryInterval?.subtotals?.[ selectedChart.key ] ||
						0,
				},
			};
		} );
	}

	getItemChartData() {
		const { stats, selectedChart, isRequesting } = this.props;

		if ( isRequesting ) {
			return [];
		}

		const intervals = stats?.primary?.data?.intervals;

		if ( ! intervals ) {
			return [];
		}

		return intervals.map( ( interval ) => {
			const intervalData = {};

			if ( ! interval?.subtotals?.segments ) {
				return intervalData;
			}

			for ( const segment of interval?.subtotals?.segments ) {
				if ( ! segment.segment_label ) {
					continue;
				}

				const label = segment.segment_label;

				intervalData[ segment.segment_id ] = {
					label,
					value: segment.subtotals[ selectedChart.key ] || 0,
				};
			}

			return {
				date: formatDate( 'Y-m-d\\TH:i:s', interval.date_start ),
				...intervalData,
			};
		} );
	}

	getTimeChartTotals() {
		const { stats, selectedChart, isRequesting } = this.props;

		if ( isRequesting ) {
			return [];
		}

		return {
			primary: stats?.primary?.data?.totals?.[ selectedChart.key ],
			secondary: stats?.secondary?.data?.totals?.[ selectedChart.key ],
		};
	}

	getChartFormats() {
		const { query, stats, isRequesting } = this.props;

		if ( isRequesting ) {
			return [];
		}

		const currentInterval = getIntervalForQuery( query );

		return getDateFormatsForInterval(
			currentInterval,
			stats?.primary?.data?.intervals?.length
		);
	}

	getChartMode() {
		const { query } = this.props;

		if ( 'compare_affiliates' === query?.filter && !! query?.affiliates ) {
			return 'item-comparison';
		}

		return 'time-comparison';
	}

	getTooltipValueFormat( type ) {
		switch ( type ) {
			case 'currency':
				return formatCurrency;
			case 'timespan':
				return formatTimeDiff;
			case 'percent':
				return formatPercent;
			default:
				return ',';
		}
	}

	render() {
		const { path, query, selectedChart, isRequesting } = this.props,
			allowedIntervals = getAllowedIntervalsForQuery( query ),
			formats = this.getChartFormats(),
			tooltipFormat = this.getTooltipValueFormat( selectedChart.type ),
			emptyMessage = _x(
				'No data available',
				'[DASHBOARD] Empty chart message',
				'yith-woocommerce-affiliates'
			);

		return (
			<Fragment>
				<Chart
					allowedIntervals={ allowedIntervals }
					data={ this.getChartData() }
					dateParser={ '%Y-%m-%dT%H:%M:%S' }
					emptyMessage={ emptyMessage }
					isRequesting={ isRequesting }
					title={ selectedChart.label }
					valueType={ selectedChart.type }
					interval={ query.interval }
					legendTotals={ this.getTimeChartTotals() }
					mode={ this.getChartMode() }
					query={ query }
					path={ path }
					tooltipLabelFormat={ formats.tooltipLabelFormat }
					tooltipValueFormat={ tooltipFormat }
					chartType={ getChartTypeForQuery( query ) }
					xFormat={ formats.xFormat }
					x2Format={ formats.x2Format }
					screenReaderFormat={ formats.screenReaderFormat }
				/>
			</Fragment>
		);
	}
}
