'use strict';

import { Component } from 'react';
import { isEqual, extend } from 'lodash';
import { Fragment } from '@wordpress/element';
import { onQueryChange } from '@woocommerce/navigation';
import { TableCard } from '@woocommerce/components';
import { formatCurrency, formatNumber, formatPercent } from '../lib/numbers';
import { getPanelUrl } from '../lib/panel';
import asyncReportsRequest from '../lib/async-requests/async-reports-request';

export default class DashboardLeaderboard extends Component {
	constructor( props ) {
		super( props );

		this.state = {
			isRequesting: true,
			stats: {},
		};

		this.update = this.update.bind( this );
	}

	componentDidMount() {
		const { query } = this.props;

		this.update( query );
	}

	componentDidUpdate( prevProps ) {
		const { query } = this.props,
			prevQuery = prevProps.query;

		if ( ! this.requiresUpdate( query, prevQuery ) ) {
			return;
		}

		this.update( query );
	}

	requiresUpdate( newQuery, oldQuery ) {
		const noUpdateProps = [ 'chart', 'chartType' ];

		for ( const prop of noUpdateProps ) {
			newQuery?.[ prop ] && delete newQuery[ prop ];
			oldQuery?.[ prop ] && delete oldQuery[ prop ];
		}

		return ! this.state.isRequesting && ! isEqual( newQuery, oldQuery );
	}

	async update( query ) {
		this.setState( { isRequesting: true } );

		const request = new asyncReportsRequest( this.props.items ),
			stats = await request.getFormattedData(
				query,
				null,
				extend( this.props.queryArgs, {
					per_page: this.props.rowsPerPage,
				} )
			);

		this.setState( {
			stats,
			isRequesting: false,
		} );
	}

	getFormattedRows() {
		const headers = this.props.headers,
			stats = this.state.stats,
			formatted = [];

		if ( ! stats.data ) {
			return formatted;
		}

		for ( const row of stats.data ) {
			const formattedRow = [];

			for ( const column of headers ) {
				formattedRow.push( this.formatCell( column, row ) );
			}

			formatted.push( formattedRow );
		}

		return formatted;
	}

	formatCell( column, row ) {
		let value, display;

		switch ( column?.key ) {
			case 'affiliate':
				const affiliateName = row?.extended_info?.name,
					affiliateAvatar = row?.extended_info?.avatar,
					affiliateEmail = row?.extended_info?.email,
					affiliateUrl = getPanelUrl( 'affiliates', null, {
						affiliate_id: row.id,
					} ),
					affiliateMailTo = `mailto:${ affiliateEmail }`;

				value = row.id;
				display = (
					<Fragment>
						<img
							width="50"
							height="50"
							src={ affiliateAvatar }
							className="avatar"
							alt={ affiliateName }
						/>
						<div className="affiliate-details">
							<a href={ affiliateUrl }>{ affiliateName }</a>
							<small className="meta email">
								<a href={ affiliateMailTo }>
									{ affiliateEmail }
								</a>
							</small>
						</div>
					</Fragment>
				);
				break;
			case 'product':
				const productName = row?.name,
					productImage = row?.extended_info?.image,
					productUrl = `post.php?post=${ row?.post_id }&action=edit`;

				value = row.id;
				display = (
					<Fragment>
						<img
							width="50"
							height="50"
							src={ productImage }
							className="avatar"
							alt={ productName }
						/>
						<div className="product-details">
							<a href={ productUrl }>{ productName }</a>
						</div>
					</Fragment>
				);
				break;
			default:
				value = row?.[ column?.key ];
				display = this.formatVal(
					value,
					column?.type,
					column?.default
				);
		}

		return {
			display,
			value,
		};
	}

	formatVal( value, type, fallback ) {
		let formatted;

		if ( ! value ) {
			value = fallback;
		}

		switch ( type ) {
			case 'currency':
				formatted = formatCurrency( value );
				break;
			case 'percent':
				formatted = formatPercent( value );
				break;
			case 'number':
				formatted = formatNumber( { precision: 0 }, value );
				break;
			default:
				formatted = value;
				break;
		}

		return formatted;
	}

	onSort( key, direction ) {
		const { query } = this.props;

		direction = query.order === 'desc' ? 'asc' : 'desc';

		onQueryChange( 'sort' )( key, direction );
	}

	render() {
		const rows = this.getFormattedRows(),
			{ query, headers, rowsPerPage, title } = this.props,
			{ isRequesting } = this.state,
			totalRows = Math.min( rows.length, rowsPerPage );

		return (
			<TableCard
				className="woocommerce-report-table woocommerce-analytics__card"
				headers={ headers }
				isLoading={ isRequesting }
				rows={ rows }
				rowsPerPage={ rowsPerPage }
				showMenu={ false }
				title={ title }
				totalRows={ totalRows }
				query={ query }
				onSort={ this.onSort }
			/>
		);
	}
}
