'use strict';

import { Component } from 'react';
import { Fragment } from '@wordpress/element';
import { leaderboards } from './config';
import { applyFilters } from '@wordpress/hooks';
import DashboardLeaderboard from '../DashboardLeaderboard';

export default class DashboardLeaderboards extends Component {
	getTitle( items ) {
		return this.getConfig( items, 'title' );
	}

	getFormattedHeaders( items ) {
		return this.getConfig( items, 'columns' );
	}

	getQueryArgs( items ) {
		return this.getConfig( items, 'queryArgs' );
	}

	getRowsPerPage( items ) {
		const perPage = this.getConfig( items, 'rowsPerPage' );

		return applyFilters(
			'yith_wcaf_dashboard_leaderboards_per_page',
			perPage || 5,
			items
		);
	}

	getConfig( items, config ) {
		return leaderboards?.[ items ]?.[ config ];
	}

	render() {
		const { path, query } = this.props,
			leaderboardsItems = [ 'products', 'affiliates' ],
			leaderboardsComponents = leaderboardsItems.map( ( items ) => (
				<DashboardLeaderboard
					key={ items }
					items={ items }
					headers={ this.getFormattedHeaders( items ) }
					path={ path }
					query={ query }
					rowsPerPage={ this.getRowsPerPage( items ) }
					title={ this.getTitle( items ) }
					queryArgs={ this.getQueryArgs( items ) }
				/>
			) );

		return (
			<Fragment>
				<div className="leaderboard-group">
					{ leaderboardsComponents }
				</div>
			</Fragment>
		);
	}
}
