'use strict';

import apiFetch from '@wordpress/api-fetch';
import { addQueryArgs } from '@wordpress/url';
import { MAX_PER_PAGE, NAMESPACE } from '../../config';
import {
	getCurrentDates,
	appendTimestamp,
	getIntervalForQuery,
} from '@woocommerce/date';

export default class asyncDataRequest {
	constructor( base ) {
		this.base = base;
		this.path = `${ NAMESPACE }/${ base }`;
		this.error = false;
		this.empty = false;
		this.requesting = false;
	}

	getRequestParams( query, dataType, args ) {
		args = args || {};
		dataType = dataType || 'primary';

		const datesFromQuery = getCurrentDates( query ),
			startDate = datesFromQuery[ dataType ].after,
			endDate = datesFromQuery[ dataType ].before,
			interval = getIntervalForQuery( query );

		return {
			order: 'asc',
			interval,
			per_page: MAX_PER_PAGE,
			page: 1,
			after: appendTimestamp( startDate, 'start' ),
			before: appendTimestamp( endDate, 'end' ),
			...args,
		};
	}

	async getData( options ) {
		try {
			this.requesting = true;

			const response = await apiFetch( {
					path: addQueryArgs( this.path, options ),
					parse: false,
				} ),
				totalResults = parseInt( response.headers.get( 'x-wp-total' ) ),
				totalPages = parseInt(
					response.headers.get( 'x-wp-totalpages' )
				),
				data = await response.json();

			this.requesting = false;

			if ( ! data ) {
				this.empty = true;
			}

			return {
				data,
				totalResults,
				totalPages,
			};
		} catch ( error ) {
			console.error( error );

			this.error = true;

			return {
				error,
			};
		}
	}

	async getFormattedData( query, dataType, args ) {
		const options = this.getRequestParams( query, dataType, args ),
			answer = await this.getData( options );

		if ( this.isError() ) {
			return {
				...answer,
				isError: true,
			};
		} else if ( this.isEmpty() ) {
			return {
				...answer,
				isEmpty: true,
			};
		}

		return answer;
	}

	isError() {
		return this.error;
	}

	isEmpty() {
		return this.empty;
	}
}
