'use strict';

import { NAMESPACE } from '../../config';
import asyncDataRequest from './async-data-request';

export default class asyncStatsRequest extends asyncDataRequest {
	constructor( base ) {
		super( base );

		this.path = `${ NAMESPACE }/reports/${ base }/stats`;
	}

	getRequestParams( query, dataType, args ) {
		let params = super.getRequestParams( query, dataType, args );

		return {
			...params,
			segmentby: query.segmentby,
			[ this.base ]: query[ this.base ],
		};
	}

	async getFormattedData( query, dataType, args ) {
		const answer = await super.getFormattedData( query, dataType, args ),
			intervals = await this.getIntervals(
				answer,
				this.getRequestParams( query, dataType, args )
			),
			totals = answer?.data?.totals;

		return {
			...answer,
			data: {
				intervals,
				totals,
			},
		};
	}

	async getIntervals( answer, options ) {
		const { data, totalResults, totalPages } = answer;
		let intervals = data?.intervals;

		if ( ! intervals ) {
			return [];
		}

		if ( totalPages > 1 ) {
			const pagedIntervals = await this.getPagedIntervals(
				answer,
				options
			);

			intervals = intervals.concat( pagedIntervals );
		}

		return intervals;
	}

	async getPagedIntervals( answer, options ) {
		const { data, totalResults, totalPages } = answer,
			promises = [];

		let currentPage = 1,
			pagedIntervals = [];

		do {
			options.page = ++currentPage;

			promises.push( this.getData( options ) );
		} while ( currentPage < totalPages );

		const pageAnswers = await Promise.all( promises );

		for ( const pageAnswer of pageAnswers ) {
			if ( ! pageAnswer?.data?.intervals ) {
				continue;
			}

			pagedIntervals = pagedIntervals.concat(
				pageAnswer?.data?.intervals
			);
		}

		return pagedIntervals;
	}

	isError() {
		return this.error;
	}

	isEmpty() {
		return this.empty;
	}
}
