'use strict';

import { addQueryArgs } from '@wordpress/url';
import apiFetch from '@wordpress/api-fetch';
import { NAMESPACE } from '../config';

export const affiliate = {
	name: 'affiliates',
	options: ( search ) => {
		const query = search
			? {
					search,
					per_page: 10,
			  }
			: {};
		return apiFetch( {
			path: addQueryArgs( `${ NAMESPACE }/affiliates`, query ),
		} );
	},
	getOptionLabel: ( option ) => <span>{ option.name }</span>,
	getOptionIdentifier: ( option ) => option.id,
	getOptionKeywords: ( option ) => [
		option.name,
		option.user_email,
		option.user_login,
		option.token,
	],
	getOptionCompletion: ( option ) => ( {
		key: option.id,
		label: option.name,
	} ),
};
