'use strict';

import { _x } from '@wordpress/i18n';
import { format as formatDate } from '@wordpress/date';

// time constants.
const MINUTE_IN_SECONDS = 60,
	HOUR_IN_SECONDS = 60 * MINUTE_IN_SECONDS,
	DAY_IN_SECONDS = 24 * HOUR_IN_SECONDS,
	WEEK_IN_SECONDS = 7 * DAY_IN_SECONDS,
	MONTH_IN_SECONDS = 30 * DAY_IN_SECONDS,
	YEAR_IN_SECONDS = 365 * DAY_IN_SECONDS;

const humanTimeDiff = ( from, to ) => {
	const fromTs = formatDate( 'U', from ),
		toTs = formatDate( 'U', to );

	if ( ! fromTs || ! toTs ) {
		return NaN;
	}

	const diff = Math.abs( toTs - fromTs );

	return formatTimeDiff( diff );
};

const formatTimeDiff = ( timeDiff ) => {
	const thresholds = [
		{
			seconds: YEAR_IN_SECONDS,
			label: _x(
				'y',
				'[DASHBOARD] Time diff',
				'yith-woocommerce-affiliates'
			),
		},
		{
			seconds: MONTH_IN_SECONDS,
			label: _x(
				'mo',
				'[DASHBOARD] Time diff',
				'yith-woocommerce-affiliates'
			),
		},
		{
			seconds: WEEK_IN_SECONDS,
			label: _x(
				'w',
				'[DASHBOARD] Time diff',
				'yith-woocommerce-affiliates'
			),
		},
		{
			seconds: DAY_IN_SECONDS,
			label: _x(
				'd',
				'[DASHBOARD] Time diff',
				'yith-woocommerce-affiliates'
			),
		},
		{
			seconds: HOUR_IN_SECONDS,
			label: _x(
				'h',
				'[DASHBOARD] Time diff',
				'yith-woocommerce-affiliates'
			),
		},
		{
			seconds: MINUTE_IN_SECONDS,
			label: _x(
				'm',
				'[DASHBOARD] Time diff',
				'yith-woocommerce-affiliates'
			),
		},
		{
			seconds: 0,
			label: _x(
				's',
				'[DASHBOARD] Time diff',
				'yith-woocommerce-affiliates'
			),
		},
	];

	for ( const threshold of thresholds ) {
		if ( timeDiff < threshold.seconds ) {
			continue;
		}

		const thresholdLabel = threshold.label,
			timesInThreshold =
				threshold.seconds > 0
					? Math.ceil( timeDiff / threshold.seconds )
					: timeDiff;

		return `${ timesInThreshold }${ thresholdLabel }`;
	}

	return NaN;
};

export { formatDate, formatTimeDiff, humanTimeDiff };
