'use strict';

import { Component } from 'react';
import { calculateDelta } from '@woocommerce/number';
import { getNewPath } from '@woocommerce/navigation';
import {
	SummaryNumber,
	SummaryList,
	SummaryListPlaceholder,
} from '@woocommerce/components';
import { formatCurrency, formatNumber, formatPercent } from '../lib/numbers';
import { formatTimeDiff } from '../lib/dates';

class SubscriptionSummary extends Component {
	getValues( key, type ) {
		const { stats } = this.props;

		let primaryValue = stats?.primary?.data?.totals?.[ key ];
		let secondaryValue = stats?.secondary?.data?.totals?.[ key ];

		return {
			delta: calculateDelta( primaryValue, secondaryValue ),
			prevValue: this.formatVal( secondaryValue, type ),
			value: this.formatVal( primaryValue, type ),
		};
	}

	formatVal( value, type ) {
		let formatted;

		switch ( type ) {
			case 'currency':
				formatted = formatCurrency( value );
				break;
			case 'percent':
				formatted = formatPercent( value );
				break;
			case 'timespan':
				formatted = formatTimeDiff( value );
				break;
			default:
				formatted = formatNumber( { precision: 0 }, value );
				break;
		}

		return formatted;
	}

	render() {
		const { isRequesting, stats, charts, selectedChart } = this.props;

		if ( isRequesting || ! stats ) {
			return <SummaryListPlaceholder numberOfItems={ charts.length } />;
		}

		return (
			<SummaryList>
				{ () => {
					return charts.map( ( chart ) => {
						const { key, order, orderby, label, type } = chart;
						const { value, prevValue, delta } = this.getValues(
							key,
							type
						);

						const newPath = { chart: key };
						if ( orderby ) {
							newPath.orderby = orderby;
						}
						if ( order ) {
							newPath.order = order;
						}
						const href = getNewPath( newPath );
						const isSelected = selectedChart.key === key;
						return (
							<SummaryNumber
								key={ key }
								label={ label }
								value={ value }
								prevValue={ prevValue }
								delta={ delta }
								href={ href }
								selected={ isSelected }
							/>
						);
					} );
				} }
			</SummaryList>
		);
	}
}

export default SubscriptionSummary;
