'use strict';

import CurrencyFactory from '@woocommerce/currency';
import { numberFormat } from '@woocommerce/number';
import { CURRENCY } from '../config';

const formatCurrency = CurrencyFactory( CURRENCY ).formatAmount;

const formatPercent = ( value ) => {
	const formatted = numberFormat( { precision: 2 }, value );

	return `${ formatted }%`;
};

export { numberFormat as formatNumber, formatCurrency, formatPercent };
