'use strict';

import { identity } from 'lodash';
import apiFetch from '@wordpress/api-fetch';
import { getIdsFromQuery } from '@woocommerce/navigation';
import { addQueryArgs } from '@wordpress/url';
import { NAMESPACE } from '../../config';
import asyncReportsRequest from './async-reports-request';
import asyncStatsRequest from './async-stats-request';

function getRequestByIdString( path, handleData = identity ) {
	return function ( queryString = '', query ) {
		const pathString = typeof path === 'function' ? path( query ) : path;
		const idList = getIdsFromQuery( queryString );

		if ( idList.length < 1 ) {
			return Promise.resolve( [] );
		}

		const payload = {
			include: idList.join( ',' ),
			per_page: idList.length,
		};

		return apiFetch( {
			path: addQueryArgs( pathString, payload ),
		} ).then( ( data ) => data.map( handleData ) );
	};
}

export const getAffiliateLabels = getRequestByIdString(
	NAMESPACE + '/affiliates',
	( affiliate ) => ( {
		key: affiliate.id,
		label: affiliate.name,
	} )
);

export const affiliatesChartData = new asyncStatsRequest( 'affiliates' );
export const affiliatesTableData = new asyncReportsRequest( 'affiliates' );
export const productsTableData = new asyncReportsRequest( 'products' );
