'use strict';

import { find, isEqual } from 'lodash';
import { Component } from 'react';
import { Fragment } from '@wordpress/element';
import { affiliatesChartData } from '../../lib/async-requests';
import { filtersConfig, chartsConfig } from './config';
import DashboardFilters from '../DashboardFilters';
import DashboardSummary from '../DashboardSummary';
import DashboardChart from '../DashboardChart';

export default class DashboardStats extends Component {
	constructor( props ) {
		super( props );

		this.state = {
			isRequesting: true,
			stats: {},
		};

		this.update = this.update.bind( this );
	}

	componentDidMount() {
		const { query } = this.props;

		this.update( query );
	}

	componentDidUpdate( prevProps ) {
		const { query } = this.props,
			prevQuery = prevProps.query;

		if ( ! this.requiresUpdate( query, prevQuery ) ) {
			return;
		}

		this.update( query );
	}

	getSelectedChart( chartKey ) {
		const chart = find( chartsConfig, { key: chartKey } );

		if ( chart ) {
			return chart;
		}
		return chartsConfig[ 0 ];
	}

	requiresUpdate( newQuery, oldQuery ) {
		const noUpdateProps = [ 'chart', 'chartType' ];

		for ( const prop of noUpdateProps ) {
			newQuery?.[ prop ] && delete newQuery[ prop ];
			oldQuery?.[ prop ] && delete oldQuery[ prop ];
		}

		return ! this.state.isRequesting && ! isEqual( newQuery, oldQuery );
	}

	async update( query ) {
		this.setState( { isRequesting: true } );

		const stats = {
			primary: await affiliatesChartData.getFormattedData(
				this.getQueryArgs( query ),
				'primary'
			),
			secondary: await affiliatesChartData.getFormattedData(
				this.getQueryArgs( query ),
				'secondary'
			),
		};

		this.setState( {
			stats,
			isRequesting: false,
		} );
	}

	getQueryArgs( query ) {
		const queryArgs = query;

		if ( 'compare_affiliates' === query.filter ) {
			queryArgs.segmentby = 'affiliate';
		}

		return queryArgs;
	}

	render() {
		const { path, query } = this.props,
			{ stats, isRequesting } = this.state,
			selectedChart = this.getSelectedChart( query?.chart );

		return (
			<Fragment>
				<DashboardFilters
					path={ path }
					query={ query }
					filters={ filtersConfig }
				/>
				<DashboardSummary
					path={ path }
					query={ query }
					charts={ chartsConfig }
					selectedChart={ selectedChart }
					stats={ stats }
					isRequesting={ isRequesting }
				/>
				<DashboardChart
					path={ path }
					query={ query }
					selectedChart={ selectedChart }
					stats={ stats }
					isRequesting={ isRequesting }
				/>
			</Fragment>
		);
	}
}
